#tutorial{
    display: inline-block;
    position: absolute;
    
    background: rgba(10, 19, 39, 1);
    border-radius: .5rem;
    text-align: center;
    padding:1rem;
    font-size:1.5rem;
    
}
#flexContainer{
    display:-ms-flexbox;
    display:flex;
    position: absolute;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    width:100vw;
    height: 100vh;
    top:0;
    left:0;
    overflow: hidden;
    
}
@-webkit-keyframes fadeIn{
    0%{
        opacity: 0;
        -webkit-transform: rotate(-180deg);
                transform: rotate(-180deg);
    }
    30%{
        opacity: 0;
    }
    100%{
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
}
@keyframes fadeIn{
    0%{
        opacity: 0;
        -webkit-transform: rotate(-180deg);
                transform: rotate(-180deg);
    }
    30%{
        opacity: 0;
    }
    100%{
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
}
@-webkit-keyframes fadeOut{
    0%{
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    80%{
        opacity: 0;
    }
    100%{
        opacity: 0;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
}
@keyframes fadeOut{
    0%{
        opacity: 1;
        -webkit-transform: rotate(0);
                transform: rotate(0);
    }
    80%{
        opacity: 0;
    }
    100%{
        opacity: 0;
        -webkit-transform: rotate(180deg);
                transform: rotate(180deg);
    }
}
@-webkit-keyframes endTutorial{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
@keyframes endTutorial{
    0%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}
#gif{
    border-radius:4px;
}
#nextButton{
    text-decoration: none;
    position: absolute;
    display: inline-block;
    right: 15px;
    top:15px;
    background: white;
    color: rgb(10, 19, 39);
    padding: 5px;
    border-radius: 4px;
    font-size: 1.5rem;
    font-weight: bold;
    cursor: pointer;
    -webkit-box-shadow: inset 0 0 10px #000000;
            box-shadow: inset 0 0 10px #000000;
    -webkit-animation: blink 600ms alternate linear infinite;
            animation: blink 600ms alternate linear infinite;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;

}
#nextButton:hover{
    -webkit-box-shadow: inset 0 0 0px #000000;
            box-shadow: inset 0 0 0px #000000;
    -webkit-animation:none;
            animation:none;
    padding: 7px;
    right:13px;
    top:13px;
}
#nextButton:active{
    padding: 5px;
    right:15px;
    top:15px;
}
@-webkit-keyframes blink{
    0%{
        -webkit-box-shadow: inset 0 0 10px #000000;
                box-shadow: inset 0 0 10px #000000;
    }
    100%{
        -webkit-box-shadow: inset 0 0 0px #000000;
                box-shadow: inset 0 0 0px #000000;
    }
}
@keyframes blink{
    0%{
        -webkit-box-shadow: inset 0 0 10px #000000;
                box-shadow: inset 0 0 10px #000000;
    }
    100%{
        -webkit-box-shadow: inset 0 0 0px #000000;
                box-shadow: inset 0 0 0px #000000;
    }
}
#filter{
    background:url(../tinified/bg.jpg);
    background-size: cover;
    position: absolute;
    width: 110%;
    height: 110%;
    -webkit-filter: blur(10px);
            filter: blur(10px);
    
}
#explainingText{
    margin-bottom:1rem;
}


